"use client"

import { Home, Account, BarChart3, FileDown, FileText, Leaf, ChevronLeft, Settings, Menu } from "lucide-react"
import { Button } from "@/components/ui/button"

export const menuItems = [
  { icon: Home, label: "Home", id: "home" },
  { icon: Account, label: "Measure", id: "measure" },
  { icon: BarChart3, label: "Analyze", id: "analyze" },
  { icon: FileDown, label: "Reduce", id: "reduce" },
  { icon: FileText, label: "Report", id: "report" },
]

interface SidebarProps {
  isExpanded: boolean
  setIsExpanded: (expanded: boolean) => void
  activeItem: string
  onItemClick: (itemId: string) => void
}

export default function Sidebar({ isExpanded, setIsExpanded, activeItem, onItemClick }: SidebarProps) {
  return (
    <>
      {/* Mobile header */}
      <div className="md:hidden flex items-center justify-between p-4 fixed top-0 left-0 right-0 z-50 bg-zinc">
        <Button
          variant="ghost"
          size="icon"
          onClick={() => setIsExpanded(!isExpanded)}
          className="text-white hover:bg-white/10"
        >
          <Menu className="h-6 w-6" />
        </Button>
        <Button variant="ghost" size="icon" className="text-white hover:bg-white/10">
          <Settings className="h-6 w-6" />
        </Button>
      </div>

      {/* Fixed Sidebar */}
      <div
        className={`
        fixed top-0 left-0 h-full z-40
        transition-all duration-300 ease-in-out
        ${isExpanded ? "w-64" : "w-16 md:w-20"}
        ${isExpanded ? "translate-x-0" : "-translate-x-full md:translate-x-0"}
      `}
      >
        <div className="h-full bg-zinc-800 backdrop-blur-sm rounded-r-3xl md:rounded-r-none md:rounded-3xl m-2 md:m-4">
          <div className="flex flex-col h-full p-4">
            {/* Logo */}
            <div className="flex items-center justify-center mb-8 mt-4">
              <div className="w-12 h-12 bg-white/20 rounded-full flex items-center justify-center backdrop-blur-sm">
                <Leaf className="h-6 w-6 text-white" />
              </div>
            </div>

            {/* Toggle button - only visible when expanded */}
            {isExpanded && (
              <div className="flex justify-end mb-4">
                <Button
                  variant="ghost"
                  size="icon"
                  onClick={() => setIsExpanded(false)}
                  className="text-white hover:bg-white/10 h-8 w-8"
                >
                  <ChevronLeft className="h-4 w-4" />
                </Button>
              </div>
            )}

            {/* Navigation items */}
            <nav className="flex-1 space-y-2">
              {menuItems.map((item) => {
                const Icon = item.icon
                const isActive = activeItem === item.id

                return (
                  <div key={item.id} className="relative">
                    <button
                      onClick={() => onItemClick(item.id)}
                      className={`
                        w-full flex items-center gap-3 p-3 rounded-xl
                        transition-all duration-300 relative z-10
                        ${isActive ? "bg-white text-zinc-800 shadow-lg" : "text-white hover:bg-white/10"}
                        ${!isExpanded ? "justify-center" : ""}
                      `}
                    >
                      <Icon className="h-5 w-5 flex-shrink-0" />
                      {isExpanded && <span className="font-medium text-sm">{item.label}</span>}
                    </button>

                    {/* Conector visual - linha branca que se estende para a direita */}
                    {isActive && (
                      <div className="absolute right-0 top-1/2 -translate-y-1/2 z-0">
                        <div
                          className="h-0.5 bg-white/80 animate-pulse"
                          style={{
                            width: `${isExpanded ? "100px" : "80px"}`,
                            transform: "translateX(100%)",
                            animation: "slideIn 0.3s ease-out forwards",
                          }}
                        />
                        {/* Ponto de conexão */}
                        <div
                          className="absolute right-0 top-1/2 -translate-y-1/2 w-2 h-2 bg-white rounded-full"
                          style={{
                            transform: `translate(${isExpanded ? "100px" : "80px"}, -50%)`,
                            animation: "fadeIn 0.5s ease-out 0.2s forwards",
                            opacity: 0,
                          }}
                        />
                      </div>
                    )}
                  </div>
                )
              })}
            </nav>

            {/* Settings button at bottom */}
            <div className="mt-auto">
              <button
                className={`
                  w-full flex items-center gap-3 p-3 rounded-xl
                  text-white hover:bg-white/10 transition-all duration-200
                  ${!isExpanded ? "justify-center" : ""}
                `}
              >
                <Settings className="h-5 w-5 flex-shrink-0" />
                {isExpanded && <span className="font-medium text-sm">Settings</span>}
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Overlay for mobile */}
      {isExpanded && (
        <div
          className="md:hidden fixed inset-0 bg-black/20 backdrop-blur-sm z-30"
          onClick={() => setIsExpanded(false)}
        />
      )}

      <style jsx>{`
        @keyframes slideIn {
          from {
            width: 0;
            opacity: 0;
          }
          to {
            width: ${isExpanded ? "100px" : "80px"};
            opacity: 0.8;
          }
        }
        
        @keyframes fadeIn {
          from {
            opacity: 0;
            transform: translate(20px, -50%) scale(0.5);
          }
          to {
            opacity: 1;
            transform: translate(${isExpanded ? "100px" : "80px"}, -50%) scale(1);
          }
        }
      `}</style>
    </>
  )
}
