"use client";

import { menuItems } from "@/components/_sidebar/sidebar";

interface MainContentProps {
  activeItem: string;
  isExpanded: boolean;
}

const contentData = {
  home: {
    title: "Dashboard Principal",
    description: "Bem-vindo ao seu painel de controle ambiental",
    content:
      "Aqui você pode visualizar um resumo geral de todas as suas métricas ambientais.",
  },
  measure: {
    title: "Medições",
    description: "Colete e registre dados ambientais",
    content:
      "Utilize nossas ferramentas para medir consumo de energia, água e outros recursos.",
  },
  analyze: {
    title: "Análise",
    description: "Analise seus dados coletados",
    content:
      "Visualize gráficos e relatórios detalhados sobre seu impacto ambiental.",
  },
  reduce: {
    title: "Redução",
    description: "Estratégias para reduzir impacto",
    content:
      "Descubra maneiras eficazes de reduzir seu consumo e impacto ambiental.",
  },
  report: {
    title: "Relatórios",
    description: "Gere relatórios detalhados",
    content:
      "Crie e exporte relatórios completos sobre suas métricas ambientais.",
  },
};

export default function MainContent({
  activeItem,
  isExpanded,
}: MainContentProps) {
  const currentContent = contentData[activeItem as keyof typeof contentData];
  const activeMenuItem = menuItems.find((item) => item.id === activeItem);

  return (
    <div
      className={`
        transition-all duration-300 ease-in-out min-h-screen pt-16 md:pt-0
        ${isExpanded ? "md:ml-72" : "md:ml-28"}
      `}
    >
      {/* Conector visual do lado do conteúdo */}
      <div className="relative">
        <div
          className="absolute left-0 top-8 w-8 h-0.5 bg-white/60 hidden md:block"
          style={{
            transform: "translateX(-100%)",
            animation: "connectSlide 0.4s ease-out 0.3s forwards",
            opacity: 0,
          }}
        />
        <div
          className="absolute left-0 top-8 w-2 h-2 bg-white rounded-full hidden md:block"
          style={{
            transform: "translate(-100%, -50%)",
            animation: "connectFade 0.3s ease-out 0.6s forwards",
            opacity: 0,
          }}
        />
      </div>

      <div className="p-6 md:p-8">
        {/* Header com ícone ativo */}
        <div className="flex items-center gap-4 mb-6">
          {activeMenuItem && (
            <div className="w-12 h-12 bg-white/10 rounded-xl flex items-center justify-center backdrop-blur-sm">
              <activeMenuItem.icon className="h-6 w-6 text-white" />
            </div>
          )}
          <div>
            <h1 className="text-3xl font-bold text-white mb-2">
              {currentContent.title}
            </h1>
            <p className="text-white/80 text-lg">
              {currentContent.description}
            </p>
          </div>
        </div>

        {/* Conteúdo principal */}
        <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-6 border border-white/20">
          <p className="text-white/90 text-lg leading-relaxed">
            {currentContent.content}
          </p>

          {/* Cards de exemplo */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 mt-8">
            {[1, 2, 3].map((i) => (
              <div
                key={i}
                className="bg-white/5 rounded-xl p-4 border border-white/10"
              >
                <div className="w-8 h-8 bg-emerald-400 rounded-lg mb-3"></div>
                <h3 className="text-white font-semibold mb-2">Card {i}</h3>
                <p className="text-white/70 text-sm">
                  Conteúdo do card relacionado a{" "}
                  {currentContent.title.toLowerCase()}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>

      <style jsx>{`
        @keyframes connectSlide {
          from {
            width: 0;
            opacity: 0;
          }
          to {
            width: 32px;
            opacity: 0.6;
          }
        }

        @keyframes connectFade {
          from {
            opacity: 0;
            transform: translate(-80%, -50%) scale(0.5);
          }
          to {
            opacity: 1;
            transform: translate(-100%, -50%) scale(1);
          }
        }
      `}</style>
    </div>
  );
}
